import React, { useEffect, useState } from 'react';

export default function Mood(props) {
  const [mood, setMood] = React.useState({});



  useEffect(() => {
    console.log(props.item);
    fetch('https://cors-anywhere.herokuapp.com/http://inmyfeels.herokuapp.com/api/mood/'+props.item.id ).then(res => res.json()).then(data => {
        setMood(data);
    });
  });

  return (
    <React.Fragment>
      <h3> Mood: {mood.mood}</h3>
    </React.Fragment>
  );
};
